import type { InlineKeyboardMarkup } from '../InlineKeyboardMarkup'
import type { InputMessageContent } from '../Input/Content/InputMessageContent'

import { BaseBuilder } from '../BaseBuilder'

export interface InlineQueryResultLocationPayload {
    type: string
    id: string
    latitude: number
    longitude: number
    title: string
    horizontal_accuracy?: number
    live_period?: number
    heading?: number
    proximity_alert_radius?: number
    reply_markup?: InlineKeyboardMarkup
    input_message_content?: InputMessageContent
    thumbnail_url?: string
    thumbnail_width?: number
    thumbnail_height?: number
}

export class InlineQueryResultLocation extends BaseBuilder<InlineQueryResultLocationPayload> implements InlineQueryResultLocationPayload {
   
    public type: string = 'location'

    public declare id: string
    public declare latitude: number
    public declare longitude: number
    public declare title: string
    public declare horizontal_accuracy?: number
    public declare live_period?: number
    public declare heading?: number
    public declare proximity_alert_radius?: number
    public declare reply_markup?: InlineKeyboardMarkup
    public declare input_message_content?: InputMessageContent
    public declare thumbnail_url?: string
    public declare thumbnail_width?: number
    public declare thumbnail_height?: number

    public setId(id: string): this {
        this.id = id
        return this
    }

    public setLatitude(latitude: number): this {
        this.latitude = latitude
        return this
    }

    public setLongitude(longitude: number): this {
        this.longitude = longitude
        return this
    }

    public setTitle(title: string): this {
        this.title = title
        return this
    }

    public setHorizontalAccuracy(horizontalAccuracy?: number): this {
        this.horizontal_accuracy = horizontalAccuracy
        return this
    }

    public setLivePeriod(livePeriod?: number): this {
        this.live_period = livePeriod
        return this
    }

    public setHeading(heading?: number): this {
        this.heading = heading
        return this
    }

    public setProximityAlertRadius(proximityAlertRadius?: number): this {
        this.proximity_alert_radius = proximityAlertRadius
        return this
    }

    public setReplyMarkup(replyMarkup?: InlineKeyboardMarkup): this {
        this.reply_markup = replyMarkup
        return this
    }

    public setInputMessageContent(inputMessageContent?: InputMessageContent): this {
        this.input_message_content = inputMessageContent
        return this
    }

    public setThumbnailUrl(thumbnailUrl?: string): this {
        this.thumbnail_url = thumbnailUrl
        return this
    }

    public setThumbnailWidth(thumbnailWidth?: number): this {
        this.thumbnail_width = thumbnailWidth
        return this
    }

    public setThumbnailHeight(thumbnailHeight?: number): this {
        this.thumbnail_height = thumbnailHeight
        return this
    }

}
